let pageSlider = new Swiper('.page .swiper.page__swiper', {
    direction: "vertical",
    slidesPerView: "auto",
    mousewheel: true,
    init: false,
    pagination: {
        el: ".page .page__swiper-pagination",
        clickable: true,
        renderBullet: function (index, className) {
            return '<span class="' + className + '">' + (index + 1) + "</span>";
        },
    },
    scrollbar: {
        el: ".page .page__swiper-scrollbar",
    },
})

const resizeHandlerSlider = () => {
    if (window.innerWidth > 992) {
        pageSlider.init()
    }
}

resizeHandlerSlider()

window.addEventListener('resize', resizeHandlerSlider);

function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '2',
        spaceBetween: 40,
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            400: {
                slidesPerView: '2',
            }
        }
    })
}

function sliderAbout() {
    var swiper = new Swiper('.about .swiper', {
        slidesPerView: '1',
        pagination: {
            el: ".about .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderEquipment() {
    var swiper = new Swiper('.equipment .swiper', {
        slidesPerView: '4',
        spaceBetween: 60,
        pagination: {
            el: ".equipment .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            575: {
                slidesPerView: '1.5',
            },
            768: {
                slidesPerView: '2',
            },
            1200: {
                slidesPerView: '3',
            },
            1400: {
                slidesPerView: '4',
            }
        }
    })
}

function sliderProjects() {
    var swiper = new Swiper('.projects .swiper', {
        slidesPerView: 'auto',
        spaceBetween: 75,
        pagination: {
            el: ".projects .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                spaceBetween: 15,
            },
            576: {
                spaceBetween: 75,
            }
        }
    })
}

function sliderProducts() {
    var swiper = new Swiper('.products .swiper', {
        slidesPerView: '2',
        spaceBetween: 40,
        pagination: {
            el: ".products .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
            },
            400: {
                slidesPerView: '2',
            }
        }
    })
}


$(document).ready(function() {

    sliderMain();
    sliderEquipment();
    sliderAbout();
    sliderProducts();
    sliderProjects();

    // Burger
    $('.header__burger').click(function () {
        $('.menu').addClass('menu--active')
        $('body').addClass('fixed-body')
    })

    $('.menu__close').click(function () {
        $('.menu').removeClass('menu--active')
        $('body').removeClass('fixed-body')
    })

    // Hover-Click
    $(".header__item").hover(onIn, onOut);
    $(".header__item .header__item-show").click(function() {
        if ($(this).parents(".header__item").hasClass("header__item--active")) {
            $(this).parents(".header__item").removeClass("header__item--active")
        } else {
            $(this).parents(".header__item").addClass("header__item--active")
        }
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tabs__content").removeClass("tabs__content--active")
        $(this).parents(".tabs").find(`.tabs__content[data-content-path="${path}"]`).addClass("tabs__content--active")
    })

    // Map


})

function onIn() {
    if (window.innerWidth > 991) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 991) {
        $(this).removeClass("header__item--active")
    }
}